var escaped = '<div style="margin:10px 0px 30px 0px;" class="alert alert-warning' + (data.temporal ? ' temporal-msg' : '') + '" role="alert">' + (data.safe ? data.r : $("<div>").text(data.r).html()) + '</div>';
